




























// import Index from "../../wxcomponents/index.vue";
import Index from '../common/index'
import { PAGE_CLOCK_DETAIL } from '../../static/page-path.js'
export default {
	components:{
		'Index' : Index
	},
	data() {
		return {
			isShowModel: true
		}
	},
	created(){
	},
	onLoad() {
		this.backBottom()
	},
	onShow(){
		this.$data.isShowModel= true
	},
	methods: {
		backBottom: function(){
			wx.pageScrollTo({
				scrollTop: 300
			})
		},
		/*
		* 关闭弹框
		* */
		_closeModel(){
			this.$data.isShowModel = false
		},
		//去打卡详情页面
		_goToClockDetail(){
			uni.navigateTo({
				url:PAGE_CLOCK_DETAIL
			})
		}
	}
}
